@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-black: #000000;
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}
nav {
  z-index: 2;
}
.logo {
  width: 5.25rem;
}
nav a.nav-link {
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}
nav ul li {
  text-transform: uppercase;
}
.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(52, 58, 64);
  border-top: 0.1875rem solid var(--primary-dark);
}
.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}
@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}
/********Header********/
.header-wraper {
  position: relative;
  background: url(/github.io/Nicholas_Port/static/media/wall-and-laptop-background.f9e274f4.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  z-index: 1;
}
.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}
@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}
/***********Typed text*******************/
.typed-text {
  font-size: 2rem;
  color: #ffffff;
  color: var(--primary-white);
}
/***********Header main offer button*******************/
.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #ffffff;
  color: var(--primary-white);
  transition: 0.2s ease-in-out;
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}
/***********About me*******************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}
.about-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}
@media (max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}
/***********Services*******************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}
.services h1 {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  text-transform: uppercase;
}
.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}
.services .icon {
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid var(--parimary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #ffffff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: 0.3125rem rgb(52, 58, 64);
  border-bottom: 0.3125rem var(--primary-dark);
}

.services .box:hover .icon {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
}
.services .box:hover .circle {
  background: #ffffff;
  background: var(--primary-white);
}
.services .box:hover p {
  color: #ffffff;
  color: var(--primary-white);
}

/************************ EXPERIENCE *********************************/

.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52, 58, 64);
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}

/*------------ MEDIA ------------------*/
@media (max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }

  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}
/************************ Portfolio *********************************/
.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}
.portfolio-image {
  width: 15rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}
.portfolio-image-box {
  position: relative;
  margin: 1rem;
}
.portfolio-wrapper h1 {
  color: #f9ab00;
  color: var(--primary-orange);
}

/************************ Overflow *********************************/
.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}
.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}
.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}
.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 7rem;
}
/************************ Popupbox *********************************/

.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}
.hyper-link {
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);
}
.hyper-link:hover {
  color: #ef4035;
  color: var(--primary-red);
}
.popupbox-title {
  text-align: center;
  margin-bottom: 1.75rem;
  color: #666666 !important;
  color: var(--primary-dark-grey) !important;
}
/************************ MEDIA *********************************/
@media (max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}
@media (max-height: 570px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media (max-height: 280px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 50%;
  }
}
/************************ Contacts *********************************/
.contacts {
  background: rgb(52, 58, 64);
  background: var(--primary-dark);
  padding: 3rem 0;
}
.contacts h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}
.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}
/************************ Contacts input *********************************/
.contacts input,
.contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #ccd1d9;
  color: var(--primary-grey);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}
.contacts .container {
  padding: 4rem 0;
}
.contacts input:focus,
.contacts textarea {
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.4rem !important;
}
.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}
.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}
input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

textarea.form-control {
  margin: -0.3rem 0;
}
.success-message {
  color: #f9ab00;
  color: var(--primary-orange);
}

@media (max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  .contacts .container {
    padding: 0.1rem;
  }
  .contacts p {
    padding: 0 0.4rem;
  }
}
/************************ FOOTER COMPONENT *********************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}

